.reviews {
    &__title {
        margin-bottom: 60px;
    }
    &__wrapper {
        display: flex;
        margin-bottom: 60px;
    }
    &__img {
        margin-right: 60px;
    }
    &__name {
        font-weight: 700;
        font-size: 18px;
        line-height: 18px;
        color: #0D0D0D;
        margin-bottom: 0;
    }
    &__count {
        display: block;
        font-weight: 500;
        font-size: 14px;
        color: #646464;
        margin-bottom: 10px;
    }
    &__text {
        font-weight: 300;
        font-size: 16px;
        color: #0D0D0D;
    }
}