@media (max-width: 992px) {
    .container {
        padding: 0 15px;
    }
    .button {  
        transition: none;
        &:hover {
            color: #fff;
            background-color: $main-color;
        }
        &:active {
            background-color: $main-color;
            color: #fff;
        }
        &_submit {
            &:hover {
                border: none;
            }
        } 
        &_mini {
            &:hover {
                border: none;
            }
        }
    }
    .header {
        &__logo {
            img {
                width: 100%;
                transition: none;
                &:hover {
                    filter: none;
                }
            }
        }
        &__phone {
            transition: none;
            &:hover {
                filter: none;
            }
        }
    }
    
    .promo {
        &__wrapper {
            align-items: center;
        }
    }
    .consultation {
        .title_left {
            text-align: center;
        }
        &__descr {
            text-align: center;
        }
        .feed-form {
            margin: 0 auto;
            margin-top: 36px;
        }
    }
    .carousel {
        &__inner {
            width: 650px;
            img {
                max-width: 650px;
            }
        }
    }
    .catalog {
        &-item {
            &__link {
                transition: none;
                &:hover {
                    color: $main-color;
                    transform: none;
                }
            }
            &__back {
                transition: none;
                &:hover {
                    color: $main-color;
                    font-size: 14px;
                }
            }
        }
        &-item:nth-child(2n) {
            margin-right: 0;
        }
        &-item:nth-child(2n+1) {
            margin-right: 20px;
        }
        &__tabs {
            margin-top: 50px;
        }
    }
    .footer {
        &__social-item {
            transition: none;
            &:hover {
                background-color: transparent;
                svg path {
                    fill: #fff;
                }
            }
        }
    }
    .modal {
        &__close {
            transition: none;
            &:hover {
                color: $main-color;
            }
            &:active {
                color: $main-color;
            }
        }
    }
}

@media (max-width: 900px) {
    .advantages {
        &__wrapper {
            flex-wrap: wrap;
        }
        &__item {
            max-width: 220px;
        }
    }
}

@media (max-width: 768px) {
    .advantages {
        &__wrapper {
            justify-content: center;
        }
        &__item {
            max-width: 400px;
            margin-bottom: 40px;
            &:last-child {
                margin-bottom: 0;
            }
        }
        &__descr {
            font-size: 13px;
        }
    }
    .carousel {
        &__inner {
            width: 100%;
            img {
                max-width: 100%;
            }
        }
    }
}

@media (max-width: 650px) {
    .promo {
        min-height: auto;
        padding: 21px 0 32px 0;
    }
    .header {
        &__official {
            display: none;
        }
    }
    .advantages {
        padding: 50px 0 50px 0;
    }
    
    .consultation {
        min-height: auto;
        padding: 60px 0 60px 0;
    }
    .carousel {
        padding: 50px 0;
    }
    .catalog {
        padding-bottom: 50px;
        &-item {
            width: 320px;
        }
        &-item:nth-child(2n+1) {
            margin-right: 0px;
        }
        &__tab {
            width: 170px;
            font-size: 16px;
        }
    }
    .reviews {
        &__wrapper {
            flex-direction: column;
            justify-content: center;
            align-items: center;
            margin-bottom: 40px;
        }
        &__img {
            margin-right: 0;
        }
        &__name {
            text-align: center;
            margin-top: 30px;
        }
        &__count {
            text-align: center;
        }
    }
}

@media (max-width: 575px) {
    .promo{
        &__header {
            font-size: 32px;
            line-height: 42px;
            text-align: center;
        }
        &__subheader {
            font-size: 20px;
            line-height: 30px;
            text-align: center;
        }
        .button_main {
            margin-top: 55px;
            width: 250px;
            height: 45px;
            font-size: 14px;
        }
        &__link {
            font-size: 14px;
        }
    }
    .title {
        font-size: 24px;
    }
    .consultation {
        &__descr {
            font-size: 16px;
        }
    }
    .catalog{
        &__tabs {
            flex-direction: column;
        }
        &__tab {
            margin: 10px 0;
            width: 270px;
        }
    }
    .footer {
        &__info {
            width: 345px;
            height: 175px;
            padding: 20px 38px 30px 38px;
        }
        &__adress {
            margin-bottom: 26px;
            font-size: 16px;
            line-height: 16px;
        }
        &__phone {
            margin-bottom: 28px;
            font-size: 18px;
            line-height: 18px;
        }
        &__social-item {
            width: 40px;
            height: 40px;
            img {
                height: 20px;
            }
        }
    }
}

@media (max-width: 480px) {
    .title {
        font-size: 22px;
    }
    .promo {
        &__icons {
            width: 100%;
        }
        &__icon {
            font-size: 12px;
        }
    }
    .header {
        flex-direction: column;
        &__contacts {
            margin-top: 20px;
        }
        .button {
            width: 100%;
            margin-top: 10px;
        }
    }
    .button_submit {
        font-size: 14px;
    }
    .advantages {
        &__item-link {
            display: block;
        }
    }
    .feed-form {
        width: 100%;
        input {
            margin-bottom: 10px;
        }
    }
    .carousel {
        .slick-dots {
            button {
                width: 13px;
                height: 13px;
            }
        }
    }
    .catalog {
        &__tab {
            width: 240px;
        }
        &-item {
            &__old-price {
                font-size: 12px;
            }
            &__price {
                font-size: 18px;
            }
        }
    }
    .footer {
        &__info {
            width: 265px;
            height: 145px;
            padding: 15px 25px;
        }
        &__adress {
            margin-bottom: 15px;
            font-size: 13px;
            line-height: 13px;
        }
        &__phone {
            margin-bottom: 20px;
        }
    }
    .modal {
        width: 90%;
        padding: 28px 20px 20px 20px;
        border-radius: 5%;
        &__close {
            top: auto;
            bottom: -35px;
            right: 50%;
            transform: translate(50%, 50%);
            color: $main-color;
            font-size: 40px;
            line-height: 20px;
            &:before {
                content: '';
                position: absolute;
                top: 0;
                left: 0;
                transform: translate(-23%, -20%);
                display: block;
                width: 40px;
                height: 40px;
                border: 1px solid #fff;
                border-radius: 100%;
            }
        }
        label.error {
            margin-bottom: 10px;
            font-size: 14px;
        }
        &__subtitle {
            font-size: 15px;
        }
        &__descr {
            font-size: 12px;
        }
    }
}