.advantages {
    min-height: 473px;
    padding: 73px 0 67px 0;
    background: #F6F6F6;
    &__title {
        margin-bottom: 60px;
    }
    &__wrapper {
        display: flex;
        justify-content: space-between;
        align-items: flex-start;
    }
    &__item {
        max-width: 272px;
    }
    &__icon {
        display: block;
        margin: 0 auto;
        margin-bottom: 30px;
        &_animated {
            animation: heartbeat 1.5s ease infinite;
            &:hover {
                animation-play-state: paused;
            }
        }
    }
    &__subtitle {
        font-weight: 700;
        font-size: 18px;
        color: #0D0D0D;
        text-align: center;
        margin-bottom: 20px;
    }
    &__descr {
        font-weight: 300;
        font-size: 14px;
        line-height: 18px;
        color: #0D0D0D;
        text-align: center;
        margin-bottom: 0;
    }
    &__item-link {
        text-decoration-line: underline;
        color: #FF0000;
        &:hover {
            color: #FF0000;
        }
    }
}