.button {
    width: 150px;
    height: 30px;
    background: $main-color;
    border-radius: 5px;
    font-weight: 500;
    font-size: 12px;
    text-transform: uppercase;
    color: #FFFFFF;
    border: none;
    transition: all 0.4s;
    &_main {
        margin-top: 67px;
        width: 300px;
        height: 50px;
        font-size: 16px;
    }
    &_submit {
        width: 100%;
        height: 55px;
        font-size: 16px;
        &:hover {
            border: 1px solid #000;
        }
    }
    &_mini {
        width: 130px;
        height: 40px;
        font-size: 14px;
        &:hover {
            border: 1px solid #000;
        }
    }
    &:hover {
        color: $main-color;
        background-color: #fff;
    }
    &:active {
        background-color: #7d7d7d;
        color: #fff;
    }
}