.header {
    display: flex;
    justify-content: space-between;
    align-items: center;

    &__logo {
        width: 251px;
        img {
            width: 100%;
            transition: all 0.4s;
            &:hover {
                filter: drop-shadow(2px 4px 6px #000);
            }
        }
    }

    &__official {
        font-weight: 500;
        font-size: 12px;
        color: #FFFFFF;
        text-align: center;
        span {
            display: block;
            font-size: 18px;
        }
    }
    &__phone {
        display: block;
        font-weight: 700;
        font-size: 18px;
        color: #FFFFFF;
        text-decoration: none;
        transition: filter 0.3s;
        &:hover {
            color: #FFFFFF;
            text-decoration: none;
            filter: drop-shadow(1px 1px 7px #000);
        }
    }
    &__contacts {
        display: flex;
        flex-direction: column;
        align-items: flex-end;
    }
}