.overlay {
    display: none;
    position: fixed;
    width: 100%;
    height: 100%;
    top: 0;
    background-color: rgba(0, 0, 0, .56);
}

.modal {
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    width: 380px;
    min-height: 400px;
    background: #F2F2F2;
    padding: 38px 40px 40px 40px;
    &__close {
        position: absolute;
        top: 0;
        right: -40px;
        font-size: 41px;
        line-height: 21px;
        color: $main-color;
        cursor: pointer;
        transition: all 0.2s;
        &:hover {
            color: #fff;
        }
        &:active {
            color: #7d7d7d;
        }
    }
    &__subtitle {
        font-weight: 700;
        font-size: 18px;
        text-align: center;
        color: #0D0D0D;
    }
    &__descr {
        font-weight: 500;
        font-size: 14px;
        text-align: center;
        color: #0D0D0D;
    }
    &_mini {
        min-height: 140px;
    }
    .error {
        border: 1px solid red
    }
    label.error {
        border: none;
        text-align: center;
        margin-bottom: 15px;
    }
}

#consultation, #order, #thanks {
    display: none;
}