.carousel {
    padding: 81px 0;
    &__inner {
        position: relative;
        width: 750px;
        margin: 0 auto;
    }
    .slick-prev, .slick-next {
        position: absolute;
        top: 50%;
        transform: translateY(-50%);
        left: -66px;
        border: none;
        background-color: transparent;
        transition: all 0.2s;
        img {
            width: 31px;
            height: 50px;
        }
        &:hover {
            filter: drop-shadow(3px 0px 6px #777777);
        }
        &:active {
            filter: drop-shadow(3px 0px 2px #303030);
        }
    }
    .slick-next {
        right: -66px;
        left: auto;
    }
    .slick-dots {
        display: flex;
        justify-content: center;
        align-items: center;
        padding-left: 0;
        list-style-type: none;
        margin-bottom: 0;
        margin-top: 20px;
        li {
            margin: 0 10px;
        }
        button {
            width: 20px;
            height: 20px;
            text-indent: -9999px;
            border: none;
            border-radius: 100%;
            background-color: #727272;
        }
    }
    .slick-active {
        button {
            background: $main-color;
        }
    }
        
}