@font-face {
    font-family: 'MuseoSansCyrl';
    font-display: swap;
    src: url('../fonts/MuseoSansCyrl-Medium.woff2') format('woff2'),
        url('../fonts/MuseoSansCyrl-Medium.woff') format('woff'),
        url('../fonts/MuseoSansCyrl-Medium.ttf') format('ttf');
    font-weight: 500;
    font-style: normal;
    font-display: swap;
}

@font-face {
    font-family: 'MuseoSansCyrl';
    font-display: swap;
    src: url('../fonts/MuseoSansCyrl-Light.woff2') format('woff2'),
